.faq {
  width: 100%;
  margin: 150px 0;
}

.faq__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  max-width: 540px;
  width: 100%;
  margin: 0 auto;
  position: relative;
}

.faq__text {
  position: relative;
}

.questions {
  display: flex;
  flex-direction: column;
  position: relative;
  margin-bottom: 50px;
}

.question {
  position: relative;
  cursor: pointer;
  pointer-events: none;
  &:last-child {
    border-bottom: 1px solid $color-grayishblue;
  }
}

.question:hover h4 {
  transition: all 0.5s ease;
  color: $color-primary-softred;
}

.question__front {
  display: flex;
  align-items: center;
  padding: 20px 0;
  border-top: 1px solid $color-grayishblue;
}

.question__front > * {
  &:not(:last-child) {
    margin-right: auto;
  }
}

.question__answer {
  // transition: all 2s ease;
  text-align: left;
  letter-spacing: 0.5px;
  line-height: 2;
  font-size: 16px;

  @media only screen and(max-width: $bp-phone-lg) {
    font-size: 16px;
  }

  @media only screen and(max-width: $bp-phone) {
    font-size: 14px;
  }
}

.closeDescription {
  width: 100%;
  margin: 0 auto;
  text-align: left;
  visibility: hidden;
  height: 0;
  overflow: hidden;
  pointer-events: none;
  transition: all 0.4s cubic-bezier(0.09, 0.58, 0.33, 0.92);
}

.openDescription {
  padding-top: 30px;
  padding-bottom: 30px;
  width: 100%;
  margin: 0 auto;
  text-align: left;
  background-color: #fff;
  color: #000;
  height: max-content;
  transition: all 1s cubic-bezier(0.16, 0.88, 0.5, 1.04);

  // @media only screen and (max-width: $bp-phone-lg) {
  //   width: 100%;
  // }
}

.arrow {
  pointer-events: all;
}

.arrowOpen {
  transform: rotate(180deg);
  filter: invert(61%) sepia(75%) saturate(4627%) hue-rotate(329deg)
    brightness(102%) contrast(96%);
}
.arrowClosed {
  transform: rotate(0deg);
}

