.features {
  width: 100%;
  text-align: center;
  position: relative;
}

.features__wrapper {
  display: flex;
  flex-direction: column;

  @media only screen and(max-width: $bp-tablet-sm) {
    height: 1100px;
  }
}

.features__text {
  max-width: 530px;
  margin: 0 auto;

  &:nth-child(1) {
    margin-bottom: 80px;
  }
}

.tabs {
  max-width: 730px;
  width: 100%;
  margin: 0 auto;
  margin-bottom: 70px;
}

.tabs__wrap {
  display: flex;
  align-items: center;
  justify-content: center;
  list-style-type: none;

  @media only screen and(max-width: $bp-tablet-sm) {
    flex-direction: column;
  }
}

.tab {
  border-bottom: 1px solid grey;
  padding: 20px 0;
  width: 100%;
  transition: all 0.4s ease;
}

.tab > * {
  margin: 25px;
  font-size: 16px;
  color: #57575f;
  transition: all 0.2s ease;
  font-weight: 500;

  &:hover {
    color: $color-primary-softred;
  }
}

.bookmark {
  width: 100%;
  // max-width: 1100px;
  display: flex;
  // margin: 20px;

  @media only screen and(max-width: $bp-tablet-sm) {
    flex-direction: column;
    margin: 0;
  }
}

.bookmark__img {
  max-width: 535px;
  // width: 100%;
  height: 347px;
  margin-right: auto;

  @media only screen and(max-width: $bp-tablet-sm) {
    margin-right: 0;
    margin: 0 auto;
    margin-bottom: 80px;
  }

  img {
    width: 100%;
    height: 100%;
  }
}

.bookmark__text {
  max-width: 430px;
  width: 100%;
  display: flex;
  flex-direction: column;
  text-align: left;
  justify-content: center;
  margin-left: 15px;

  @media only screen and(max-width: $bp-tablet-sm) {
    text-align: center;
    margin: 0 auto;
  }
}

.bookmark__text > * {
  &:not(:last-child) {
    margin-bottom: 40px;
  }
}

.bookmarks {
  display: flex;
  // overflow-x: hidden;
  width: 100%;
  height: 420px;
  position: relative;

  @media only screen and(max-width: $bp-tablet-sm) {
    height: auto;
  }
}

.bookmark-primary {
  position: absolute;
  top: 0;
  left: 0;
}

.bookmark-secondary {
  position: absolute;
  top: 0;
  left: 0;
}

.bookmark-tertiary {
  position: absolute;
  top: 0;
  left: 0;
}

.show {
  display: flex;
  transition: all 1s ease;
  animation: opacityChange 2s;
}

.hide {
  transition: all 1s ease;
  display: none;
}

.borderBotRed {
  border-bottom: 3px solid $color-primary-softred;
}
