.footer {
  background-color: $color-verydarkblue;
  width: 100%;
}

.footer__wrapper {
  display: flex;
  align-items: center;
  padding: 38px 0;

  @media only screen and(max-width:$bp-tablet-sm) {
    flex-direction: column;
  }
}

.footer__logo {
  margin-right: 60px;
  @media only screen and(max-width:$bp-tablet-sm) {
    margin-right: 0;
    margin-bottom: 30px;
  }
}

.footer__links {
  list-style-type: none;
  display: flex;
  color: #fff;
  text-transform: uppercase;
  margin-right: auto;
  letter-spacing: 1px;

  @media only screen and(max-width:$bp-tablet-sm) {
    flex-direction: column;
    justify-content: center;
    margin: 0 auto;
    align-items: center;
    margin-bottom: 40px;
  }
}

.footer__link {
  transition: color 0.4s ease;
  cursor: pointer;
  &:not(:last-child) {
    margin-right: 40px;

    @media only screen and(max-width:$bp-tablet-sm) {
      margin-right: 0;
      margin-bottom: 40px;
    }
  }

  &:hover {
    color: $color-primary-softred;
  }
}

.footer__socials {
  display: flex;
  align-items: center;
  list-style-type: none;
}

.facebookIcon {
  &:hover {
    filter: invert(55%) sepia(55%) saturate(4144%) hue-rotate(326deg)
      brightness(97%) contrast(100%);
  }
}

.twitterIcon {
  margin-left: 40px;

  &:hover {
    filter: invert(55%) sepia(55%) saturate(4144%) hue-rotate(326deg)
      brightness(97%) contrast(100%);
  }
}

