$color-primary-softblue: rgb(83, 104, 223);
$color-primary-softred: #fa5757;

$color-grayishblue: rgb(145, 148, 161);
$color-verydarkblue: rgb(37, 43, 70);

$font-primary: "Rubik", sans-serif;

$font-size: 18px;

//Responsive breakpoints
$bp-laptop: 1280px;
$bp-tablet: 1024px;
$bp-tablet-sm: 768px;
$bp-phone-lg: 480px;
$bp-phone: 375px;
$bp-phone-sm: 320px;

$max-content-width: 1100px;

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  scroll-behavior: smooth;
  box-sizing: border-box;
}

body {
  font-family: $font-primary;
}

.attribution {
  font-size: 11px;
  text-align: center;
}

.attribution a {
  color: hsl(228, 45%, 44%);
}

.container {
  max-width: calc($max-content-width + 40px);
  width: 100%;
  padding: 0 20px;
  margin: 0 auto;

  // &--tabs {
  //   overflow: hidden;
  // }
}

.container1440 {
  max-width: 1440px;
  width: 100%;
  padding: 0 20px;
  margin: 0 auto;
  position: relative;
}

a,
a:hover,
a:focus,
a:active {
  text-decoration: none;
  color: inherit;
}

