.heading-1 {
  font-size: 36px;
  font-weight: 500;
  color: $color-verydarkblue;
}

.heading-2 {
  font-size: 28px;
  font-weight: 500;
  letter-spacing: 0.5px;
  margin-bottom: 40px;

  &--contact {
    max-width: 420px;
    width: 100%;
    text-align: center;
    color: #fff;
  }
}

.heading-3 {
  font-size: 16px;
  margin-bottom: 20px;
  font-weight: 500;
  color: $color-verydarkblue;
}

.heading-4 {
  // margin-bottom: 20px;
  font-size: 18px;
  font-weight: 500;
  color: $color-verydarkblue;

  &--contact {
    letter-spacing: 2px;
    text-transform: uppercase;
    margin-bottom: 50px;
    color: #fff;
    font-weight: 400;

    @media only screen and(max-width:$bp-phone-lg) {
      margin-bottom: 23px;
    }
  }

  @media only screen and(max-width: $bp-phone-lg) {
    font-size: 16px;
  }

  @media only screen and(max-width: $bp-phone) {
    font-size: 14px;
  }
}

.paragraph {
  font-size: 16px;
  color: $color-grayishblue;
  margin-bottom: 40px;
}
