.hero {
  width: 100%;
  // background-color: grey;
  margin-top: 100px;
  margin-bottom: 100px;
  position: relative;
}

.hero__wrapper {
  display: flex;
  // background-color: rgb(255, 255, 255);
  width: 100%;

  @media only screen and(max-width:$bp-tablet-sm) {
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
}

.hero__text {
  max-width: 480px;
  // width: 100%;
  display: inherit;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-right: auto;
  @media only screen and(max-width:$bp-tablet-sm) {
    margin-right: 0;
    order: 2;
  }
}

.hero__text > * {
  @media only screen and(max-width:$bp-tablet-sm) {
    text-align: center;
  }

  &:not(:last-child) {
    margin-bottom: 40px;
  }
}

.hero__img {
  max-width: 575px;
  position: relative;

  img {
    width: 100%;
    z-index: 3;
    position: relative;

    @media only screen and(max-width:$bp-tablet-sm) {
      z-index: -2;
    }
  }
}

.hero__btns {
  align-self: flex-start;

  @media only screen and(max-width:$bp-tablet-sm) {
    align-self: center;
  }
}

.blue {
  width: 500px;
  height: 350px;
  border-radius: 50% 0 0 50%;
  background-color: $color-primary-softblue;
  position: absolute;
  right: 0;
  bottom: 0;
  z-index: -21;

  @media only screen and(max-width: 1200px) {
    width: 300px;
  }
  @media only screen and(max-width: $bp-tablet-sm) {
    width: 270px;
    height: 300px;
    top: 80px;
  }

  @media only screen and(max-width: $bp-phone-lg) {
    width: 270px;
    height: 200px;
    top: 100px;
  }
}

.blue2 {
  width: 500px;
  height: 350px;
  border-radius: 0 50% 50% 0%;
  background-color: $color-primary-softblue;
  position: absolute;
  left: 0;
  bottom: 0;
  z-index: -21;

  @media only screen and(max-width: 1200px) {
    width: 300px;
  }
  @media only screen and(max-width: $bp-tablet-sm) {
    width: 270px;
    height: 300px;
    bottom: 170px;
  }

  @media only screen and(max-width: $bp-phone-lg) {
    width: 270px;
    height: 200px;
    bottom: 170px;
  }
}
