.contact {
  width: 100%;
  background-color: $color-primary-softblue;
}

.contact__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 70px 0;
}

.form {
  display: flex;
  justify-content: center;
  // align-items: center;
  width: 100%;
  // margin: 0 auto;

  @media only screen and(max-width:$bp-phone-lg) {
    flex-direction: column;
    align-items: center;
  }
}

input[type="email"] {
  max-width: 300px;
  width: 100%;
  height: 48px;
  border-radius: 5px;
  background-color: #fff;
  color: $color-grayishblue;
  outline: none;
  border: none;
  margin-right: 15px;
  padding-left: 20px;
  font-size: 18px;

  @media only screen and(max-width:$bp-phone-lg) {
    margin-bottom: 15px;
    margin-right: 0;
    max-width: 310px;
  }
}

input[type="submit"] {
  max-width: 128px;
  width: 100%;
  height: 48px;
  border-radius: 5px;
  border: none;
  background-color: $color-primary-softred;
  color: #fff;
  font-size: 16px;
  font-weight: bold;
  transition: background-color 0.5s ease, color 0.5s ease;
  cursor: pointer;

  &:hover {
    border: 2px solid $color-primary-softred;
    background-color: #fff;
    color: $color-primary-softred;
  }

  @media only screen and(max-width:$bp-phone-lg) {
    max-width: 310px;
    font-size: 18px;
  }
}
