.extensions {
  width: 100%;
  margin: 100px 0;
}

.extensions__wrapper {
  display: flex;
  align-items: center;
  flex-direction: column;
  width: 100%;
}

.extensions__text {
  text-align: center;
}

.extensions__items {
  display: flex;
  align-items: center;
  // justify-content: space-between;
  width: 100%;

  @media only screen and (max-width: $bp-tablet-sm) {
    flex-direction: column;
  }
}

.item {
  margin: 0 auto;
  max-width: 280px;
  width: 30%;
  // height: 374px;
  display: flex;
  flex-direction: column;
  align-items: center;
  -webkit-box-shadow: 0px 9px 22px 10px rgba(156, 156, 156, 1);
  -moz-box-shadow: 0px 9px 22px 10px rgba(156, 156, 156, 1);
  box-shadow: 0px 9px 22px 10px rgba(156, 156, 156, 1);
  border-radius: 10px;

  @media only screen and (max-width: $bp-tablet-sm) {
    width: auto;
    margin: 0;
  }

  @media only screen and (max-width: $bp-tablet-sm) {
  }

  &:nth-child(2) {
    margin-top: 40px;
  }
  &:nth-child(3) {
    margin-top: 80px;
  }

  &:not(:last-child) {
    margin-right: 40px;

    @media only screen and (max-width: $bp-tablet-sm) {
      margin-right: 0;
    }
  }
}

.item__img {
  margin-top: 40px;
  margin-bottom: 40px;
}

.dots {
  max-width: 280px;
  width: 100%;
  img {
    width: 100%;
  }
}
