.btn {
  text-transform: uppercase;
  border: none;
  outline: none;
  // cursor: pointer;
  cursor: pointer;
  background-color: transparent;
  font-size: 16px;
  font-weight: 500;
  font-family: $font-primary;
}

.btn__login {
  width: 110px;
  height: 40px;
  background-color: $color-primary-softred;
  color: #fff;
  border-radius: 5px;
  letter-spacing: 1px;
  border: none;
  transition: background-color 0.5s ease, color 0.5s ease;

  &:hover {
    border: 2px solid $color-primary-softred;
    background-color: #fff;
    color: $color-primary-softred;
  }

  @media only screen and(max-width: $bp-tablet-sm) {
    width: 100%;
    margin-top: 24px;
    background-color: transparent;
    border: 2px solid white;
  }
}

.hero__btn {
  text-transform: none;
  width: 166px;
  height: 48px;
  border-radius: 5px;
  -webkit-box-shadow: 0px 8px 19px -2px rgba(0, 0, 0, 0.72);
  -moz-box-shadow: 0px 8px 19px -2px rgba(0, 0, 0, 0.72);
  box-shadow: 0px 8px 19px -2px rgba(0, 0, 0, 0.72);
  transition: box-shadow 0.6s ease;

  &:hover {
    -webkit-box-shadow: 0px 0px 0px 0px #000000;
    box-shadow: 0px 0px 2px 0px #000000;
  }

  @media only screen and(max-width:$bp-phone-lg) {
    width: 150px;
  }

  &--purple {
    background-color: $color-primary-softblue;
    margin-right: 20px;
    color: #fff;

    @media only screen and(max-width:$bp-tablet-sm) {
      margin-right: 15px;
    }

    @media only screen and(max-width:$bp-phone-lg) {
      margin-right: 15px;
      // margin-bottom: 15px;
    }

    @media only screen and(max-width:$bp-phone) {
      // margin-right: 15px;
      margin-right: 0;
      margin-bottom: 15px;
      // margin-bottom: 15px;
    }
  }

  &--white {
    background-color: #f7f7f7;
    color: $color-verydarkblue;
  }
}

.btn__more {
  background-color: $color-primary-softblue;
  color: #fff;
  width: 114px;
  height: 48px;
  border-radius: 5px;
  text-transform: capitalize;
  -webkit-box-shadow: 0px 8px 19px -2px rgba(0, 0, 0, 0.72);
  -moz-box-shadow: 0px 8px 19px -2px rgba(0, 0, 0, 0.72);
  box-shadow: 0px 8px 19px -2px rgba(0, 0, 0, 0.72);
  transition: box-shadow 0.6s ease;

  &:hover {
    -webkit-box-shadow: 0px 0px 0px 0px #000000;
    box-shadow: 0px 0px 2px 0px #000000;
  }

  &--faq {
    background-color: $color-primary-softblue;
    color: #fff;
    width: 114px;
    height: 48px;
    border-radius: 5px;
    text-transform: capitalize;
    -webkit-box-shadow: 0px 8px 19px -2px rgba(0, 0, 0, 0.72);
    -moz-box-shadow: 0px 8px 19px -2px rgba(0, 0, 0, 0.72);
    box-shadow: 0px 8px 19px -2px rgba(0, 0, 0, 0.72);
    transition: box-shadow 0.6s ease;

    &:hover {
      -webkit-box-shadow: 0px 0px 0px 0px #000000;
      box-shadow: 0px 0px 2px 0px #000000;
    }
  }

  @media only screen and(max-width: $bp-tablet-sm) {
    display: none;
  }
}

.btn__ext {
  max-width: 232px;
  width: 100%;
  height: 48px;
  background-color: $color-primary-softblue;
  color: #fff;
  text-transform: none;
  border-radius: 5px;
  margin: 20px 0;

  // -webkit-box-shadow: 0px 8px 19px -2px rgba(0, 0, 0, 0.72);
  // -moz-box-shadow: 0px 8px 19px -2px rgba(0, 0, 0, 0.72);
  // box-shadow: 0px 8px 19px -2px rgba(0, 0, 0, 0.72);
  transition: color 0.6s ease, background-color 0.6s ease;

  &:hover {
    //   -webkit-box-shadow: 0px 0px 0px 0px #000000;
    //   box-shadow: 0px 0px 2px 0px #000000;
    background-color: #fff;
    color: $color-primary-softblue;
    border: 2px solid $color-primary-softblue;
  }
}
